;cursor:pointer;font-size:medium;font-size:initial;padding:0.5rem 1rem}.Filter__value{margin:0 0.4rem}.Header{align-items:center;display:flex;justify-content:right;padding:1rem;z-index:1}.Header__search-by-keyword{background-color:transparent;border:0 none;color:#fff;font-size:16px;height:30px;outline:0;width:300px;font-family:"Hind", sans-serif;padding:0}.Profile{padding:2rem;text-align:center}.Profile__name{margin-bottom:0;font-family:"Oswald", sans-serif;font-weight:500;text-transform:uppercase}.Profile__description{font-size:20px;line-height:2rem}.Profile__toggle-theme{position:absolute;top:0;right:100px;width:24px;height:25px;border-radius:50%;border:0 none;cursor:pointer;-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out;background-color:transparent}.SocialNetwork{padding:0;margin:0;text-align:center}.SocialNetwork__item{display:inline-block;padding:0 0.6rem}.SocialNetwork__link{font-size:1.5rem}.SocialNetwork__link>div{-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out}.SocialNetwork__link>div:hover{-webkit-transform:scale(1.25);transform:scale(1.25)}.Tag{padding:0.25rem 0.75rem;border-radius:4px;margin:0.5rem 0.5rem 0 0;font-size:12px;text-transform:uppercase;display:flex;align-items:center;justify-content:center;height:1rem}.Thumbnail{object-fit:cover;overflow:hidden;margin:auto}.App-theme--light{background-color:#f2f2f2;color:#333}.App-theme--light .App__left-column{background-color:#fff}.App-theme--light .Card{color:#666;background-color:#fff}.App-theme--light .Card__name{color:#333}.App-theme--light .Header{background-color:#f2f2f2;box-shadow:800px -100px 20px 100px #ccc}.App-theme--light .Header__search-by-keyword{color:#333;border-bottom:1px solid rgba(51,51,51,0.1)}.App-theme--light .Filter__trigger{background-color:#e6e6e6;color:#333}.App-theme--light .SocialNetwork__link{color:#4d4d4d}.App-theme--light .Tag{background-color:#e6e6e6}.App-theme--light .Profile__toggle-theme{box-shadow:0 0 0 5px inset #333}.App-theme--light .Profile__toggle-theme:hover{box-shadow:0 0 0 3px inset #333}.App-theme--dark{background-color:#262626;color:#fff}.App-theme--dark .App__left-column{background-color:#333}.App-theme--dark .Card{color:#fff;background-color:#333}.App-theme--dark .Card__name{color:#fff}.App-theme--dark .Header{background-color:#262626;box-shadow:800px -100px 20px 100px #000}.App-theme--dark .Header__search-by-keyword{color:#fff;border-bottom:1px solid rgba(255,255,255,0.1)}.App-theme--dark .Filter__trigger{background-color:#1a1a1a;color:#fff}.App-theme--dark .SocialNetwork__link{color:#fff}.App-theme--dark .Tag{background-color:#1a1a1a}.App-theme--dark .Profile__toggle-theme{box-shadow:0 0 0 5px inset #fff}.App-theme--dark .Profile__toggle-theme:hover{box-shadow:0 0 0 3px inset #fff}

